@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700|Poppins:400,600,700");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  color: #111111;
  font-size: 16px;
  line-height: 1.6;
  font-family: 'Poppins', sans-serif;
}

a {
  text-decoration: none;
  color: #111111;
  transition: all 200ms ease-in-out;
}

a:hover {
  color: #FF851B;
}

.full-height {
  height: 100%;
}

.flex {
  display: flex;
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.flex-space--between {
  justify-content: space-between;
}

.flex-space--around {
  justify-content: space-around;
}

.flex-align--center {
  align-items: center;
}

.flex-align--end {
  align-items: flex-end;
}

.flex-direction--column {
  flex-direction: column;
}

.flex-end {
  justify-content: flex-end;
}

.flex-flow--wrap {
  flex-flow: wrap;
}

.showcase {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: #1e1e1e;
}

.showcase-panel {
  width: 80%;
  border-radius: 4px;
  height: 600px;
  overflow: hidden;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.panel {
  position: relative;
  padding: 0 5%;
  padding-top: 25px;
  color: #111111;
  background: #fff;
}

.panel-logo {
  margin-bottom: 60px;
  font-size: 18px;
  font-weight: 700;
}

.panel-nav--list {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.panel-nav--list li {
  margin-right: 20px;
}

.panel-nav--list li:last-child {
  margin-right: 0;
}

.panel-nav--list a {
  color: #111111;
  font-size: 15px;
  font-weight: 600;
}

.panel-title {
  font-size: 61px;
  line-height: 1;
  position: relative;
}

.panel-title:after {
  content: '';
  display: block;
  width: 50px;
  height: 5px;
  background: #01FF70;
  margin-left: auto;
  margin-top: 5px;
}

.panel-desc {
  font-size: 14px;
  font-weight: 600;
  padding-left: 60px;
  color: rgba(17, 17, 17, 0.4);
  line-height: 1.8;
}

.panel-subtitle {
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 1px;
  font-weight: 600;
}

.panel-left {
  width: 55%;
}

.panel-right {
  width: 45%;
}

.panel-bottom {
  position: absolute;
  bottom: 0;
  left: 50%;
  background: pink;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 90%;
  border-radius: 3px 3px 0 0;
  height: 350px;
  transform: translateX(-50%);
}

.panel-bottom--title {
  font-size: 40px;
  text-align: center;
  display: none;
}

.panel-bottom--block {
  position: absolute;
  right: 0;
  top: -60px;
  width: 120px;
  height: 120px;
  background: #FF4136;
  padding: 15px;
}

.panel-bottom--block span {
  color: #fff;
}

.panel-bottom--block a {
  font-size: 13px;
  font-weight: 600;
  color: #fff;
  margin-top: 15px;
  display: block;
}

.panel-bottom--block a svg {
  margin-left: 10px;
  font-size: 14px;
}
